body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 30px;
  display: grid;
}

.person {
  background: #fff;
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.person__photo {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  margin-top: 30px;
  position: relative;
  overflow: hidden;
}

.person__photo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.person__rank {
  color: #8b0000;
  background: #fff;
  border: 2px solid #eee;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
}

.person__name {
  color: #2f4f4f;
  text-align: center;
  letter-spacing: 0%;
  margin-bottom: 0;
  font-size: 20px;
  font-weight: 700;
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1200px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
  }
}
